@use 'base/variables' as v;
@use 'base/mixins' as m;

.tour {
    border: .1rem solid transparentize(v.$light, .7);
    border-radius: .5rem;
    overflow: hidden;

    &__content {
        
        @include m.device(v.$LDesktop) {
            display: grid;
            grid-template-columns: repeat(1, 85rem 30rem);
            justify-content: space-between;
        }
    }
    
    @include m.device(v.$desktop) {
        height: 45rem;
        display: flex;
    }


    &__graphic {
        display: flex;
        flex-grow: 2;
        position: relative;
        overflow: hidden;
        
        @include m.device(v.$desktop) {
            min-width: 35rem;
            width: 35rem;
        }
    }

    &__image {
        aspect-ratio: 16/9;
        @include m.device(v.$desktop) {
            aspect-ratio: unset;
            width: 100%;
            height: 100%;
            flex-shrink: 0;
            flex-grow: 0;
            transition: translate .3s ease-in-out;
        }
    }

    &__btn {
        all: unset;
        display: block;
        position: absolute;
        top: 50%;
        transform: translate(0, -50%);
        padding: 3rem;
        border-radius: 50%;
        transition: .1s ease;
        cursor: pointer;
        &:hover {
            background-color: transparentize(#000, .5);
        }

        &:focus, &:focus-visible {
            outline: none;
        }

        &>* {
            height: 2.4rem;
            width: 2.4rem;
        }
    }

    &__flex {
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding: 2rem;
    }

    &__icon {
        display: flex;
        gap: 1rem;
        align-items: center;
    }

    &__desc {
        height: fit-content;
        overflow: auto;
        margin-bottom: 2rem
    }

    &__button {
        &-more {
            @include m.button(v.$darkFontColor, v.$dark);

            @include m.scheme() {
                
                @include m.button(v.$lightFontColor, v.$light);
            }

            @include m.device(v.$tablet) {
                max-width: 20rem;
            }
        }
    }

    &__price {
        font-family: v.$secundaryFont;
        font-size: 2.4rem;
        font-weight: 900;
    }
}