@use 'base/variables' as v;
@use 'base/mixins' as m;

.trust {
    &__grid {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(30rem, 1fr));
        gap: 2rem;
    }

    &__image {
        width: 10rem;
        margin: 0 auto;
        
    }

    &__heading, &__text {
        text-align: center;
    }

    &__heading {
        margin-top: 3rem;
    }
}